.Notes {
    
    padding: 2rem;
    max-width: 30rem;
    font-size: 1.6rem;
    margin: 2rem auto;
}

.Notes-Create {
    width: 100%;
}

.Notes-Create-input{
    width: 100%;
    height: 6rem;
    margin-bottom: 1rem;
}

.Notes-Create-Buttons {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.Notes-Create-Button {
    font-weight: 600;
    padding: 0.5rem 1.5rem;
    background-color: blueviolet;
    color: white;
    cursor: pointer;
}

.Notes-Create-Button-reset {
    background-color: rgba(128, 128, 128, 0.788);
}

.Notes-Note {
    width: 100%;
    position: relative;
    padding: 1rem;
    border: 1px dashed gray;
    margin-bottom: 1rem;
}

.Notes-Note-Delete {
    position: absolute;
    top: -1rem;
    right: -1rem;
    width: 1.6rem;
    height: 1.6rem;
    background-color: rgba(71, 67, 66, 0.719);
    color: white;
    text-align: center;

    
    line-height: 1.5rem;
    border-radius: 50%;
    cursor: pointer;
    font-size: 85%;
   
}